* {
  padding: 0;
  margin: 0;
  transition: filter 0.05s linear;
  scroll-behavior: smooth;
  word-break: break-word;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

html, body {
  font-family: Inconsolata-Regular;
  color: white;
  height: 100%;
  width: 100%;
}

body {
  font-size: 14px;
}

img {
  aspect-ratio: auto;
}

a {
  text-decoration: none;
  color: white;
}

a:hover, button:hover {
  cursor: pointer;
  filter: brightness(80%);
}

#root {
  height: 100%;
  width: 100%;
  overflow: auto;
}

button {
  border: none;
  font-weight: bold;
  background-color: #9A0000;
  color: #FFFFFF;
  border-radius: 0.3rem;
  font-family: Inconsolata-SemiBold;
}

::-webkit-scrollbar-thumb {
  background-color: #F5333F;
}

::-webkit-scrollbar-track {
  background-color: #9A0000;
}

::-webkit-scrollbar {
  width: 1em;
}

@font-face {
  font-family: Inconsolata-Regular;
  src: url("./assets/static/Inconsolata/Inconsolata-Regular.ttf");
}
@font-face {
  font-family: Inconsolata-Bold;
  src: url("./assets/static/Inconsolata/Inconsolata-Bold.ttf");
}
@font-face {
  font-family: Inconsolata-SemiBold;
  src: url("./assets/static/Inconsolata/Inconsolata-SemiBold.ttf");
}
@media screen and (max-width: 1366px) {
  * {
    overflow: hidden;
  }
  .page {
    height: auto;
    max-width: 100%;
  }
  .pageTitle {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  #logoSection {
    font-size: 1.5rem;
  }
  #navbar-content {
    justify-content: center;
  }
  #navSelectionSection {
    display: none;
  }
  #landingPageContent {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    gap: 3rem;
  }
  .pageContent {
    width: 100%;
  }
  .pageContent_Center {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-align: center;
    gap: 1rem;
  }
  .pageContent_Left {
    display: none;
  }
  .pageContent_Right {
    display: none;
  }
  #welcomeMessageContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: 1.25rem;
    gap: 1.1rem;
  }
  #welcomeMessageContent p {
    font-size: 1.25rem;
  }
  #welcomeImage {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #checkOutProjectButton {
    padding: 1rem 1rem;
    font-size: 1.25rem;
  }
  .skillCard {
    width: 8rem;
  }
  .skillCard img {
    width: 100%;
  }
  #projectSection {
    max-height: auto;
  }
  .projectRow {
    display: flex;
    flex-direction: column;
    max-width: 75%;
  }
  .projectRow img {
    max-width: 100%;
  }
  .projectDescription p {
    margin-bottom: 1.5rem;
  }
  #aboutSection {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #aboutDescription {
    max-width: 75%;
  }
  #hobbySection span {
    font-size: 1.25rem;
  }
  #contactSelection {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0rem;
  }
  #contactSection {
    max-width: 90%;
  }
  #contactSection h2 {
    font-size: 1.5rem;
  }
  #linkedIn {
    margin: 0;
    padding: 1rem 0rem 0rem 0rem;
  }
  #email {
    padding: 1.75rem 1rem 5rem 1rem;
  }
